@mixin componentCenter($pos,$w:auto,$h:auto){
    position: $pos;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: $w;
    height: $h;
    transition: 0.5s;
}

@mixin borderRadius($x1,$x2,$x3,$x4,$y1,$y2,$y3,$y4) {
    $val:#{$x1 $x2 $x3 $x4} / #{$y1 $y2 $y3 $y4};
    border-radius: $val;
    -webkit-border-radius:$val;
        -moz-border-radius:$val;
        -ms-border-radius:$val;
        -o-border-radius:$val;
}

.hideScrollBar{
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
    &::-webkit-scrollbar {  //Chrome Safari Opera
        display: none;
    }            
}